<template><section>
<h2 id="utils-gong-ju-ku"><a class="header-anchor" href="#utils-gong-ju-ku" aria-hidden="true">¶</a> Utils 工具库</h2>
<p>常用的工具函数。</p>
<h3 id="ji-ben-shi-yong"><a class="header-anchor" href="#ji-ben-shi-yong" aria-hidden="true">¶</a> 基本使用</h3>
<p>在 <code v-pre="">main.js</code> 中写入以下内容：</p>
<pre v-pre=""><code class="hljs language-html">import Vue from 'vue'
import fmutils from fm-vue-ui/lib/utils/index';
import App from './App.vue'

Vue.prototype.$utils = fmutils;

new Vue({
  el: '#app',
  render: h =&gt; h(App)
})
</code></pre>
<p><code v-pre="">fmutils</code> 目前提供的 API 列表如下:</p>
<pre v-pre=""><code class="hljs language-js"><span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    formatDateToStr,  <span class="hljs-comment">// 时间格式化</span>
    avatarError,  <span class="hljs-comment">// 设置图像加载失败时的默认图像</span>
    <span class="hljs-comment">// 下面三个是对 Storage 的封装, 均提供 set/get/remove/clear/getAll 五个 api</span>
    localStorage,  <span class="hljs-comment">// ==&gt; window.localStorage</span>
    sessionStorage, <span class="hljs-comment">// ==&gt; window.sessionStorage</span>
    memoryStorage <span class="hljs-comment">// ==&gt; 存在内存中的一个 map 对象</span>
};
</code></pre>
<h3 id="mo-ren-tu-xiang"><a class="header-anchor" href="#mo-ren-tu-xiang" aria-hidden="true">¶</a> 默认图像</h3>
<docs-demo-block> 
                                                <div class="source" slot="source"><img src="xxx.png" @error="avatarError">
<img src="xxx.png" @error="avatarError2">

   
</div>
                                                <p>用于图像加载失败时显示统一的默认图像。默认图像是 Base64 格式的。也可以指定一个 url。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"xxx.png"</span> <span class="hljs-attr">@error</span>=<span class="hljs-string">"avatarError"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">img</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">src</span>=<span class="hljs-string">"xxx.png"</span> <span class="hljs-attr">@error</span>=<span class="hljs-string">"avatarError2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">img</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        avatarError() {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.$utils.avatarError(e);
        },

        avatarError2 () {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.$utils.avatarError(e, <span class="hljs-string">'//www.followme.com/portalindex/assets/logo.png'</span>);
        }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>   
</code></pre>
</docs-demo-block>
<h3 id="shi-jian-ge-shi-hua-shu-chu"><a class="header-anchor" href="#shi-jian-ge-shi-hua-shu-chu" aria-hidden="true">¶</a> 时间格式化输出</h3>
<docs-demo-block> 
                                                <div class="source" slot="source"><p>默认的当前时间格式：<span v-text="getCurDate()"></span></p>
<p>默认输出的格式化时间：<span>{{formatDate()}}</span></p>
<p>指定格式输出：<span>{{formatDate(&apos;yyyy/MM/dd hh:mm:ss&apos;)}}</span></p>
<p>只输出时间：<span>{{formatDate(&apos;hh:mm:ss&apos;)}}</span></p>
<p>(只有一个 M/m/h/m/s等时)不加0：<span>{{formatDate(&apos;hh:mm:s&apos;)}}</span></p>

   
</div>
                                                <p>接受一个 Date 实例和格式输出。默认的输出格式是 <code>yyyy-MM-dd</code>。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>默认的当前时间格式：<span class="hljs-tag">&lt;<span class="hljs-name">span</span> <span class="hljs-attr">v-text</span>=<span class="hljs-string">"getCurDate()"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>默认输出的格式化时间：<span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>{{formatDate()}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>指定格式输出：<span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>{{formatDate('yyyy/MM/dd hh:mm:ss')}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>只输出时间：<span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>{{formatDate('hh:mm:ss')}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>(只有一个 M/m/h/m/s等时)不加0：<span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>{{formatDate('hh:mm:s')}}<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        getCurDate() {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(<span class="hljs-string">"2017-12-05T04:26:44.584Z"</span>);
        },

        formatDate (formatStr = <span class="hljs-string">''</span>) {
            <span class="hljs-keyword">return</span> <span class="hljs-keyword">this</span>.$utils.formatDateToStr(<span class="hljs-keyword">new</span> <span class="hljs-built_in">Date</span>(<span class="hljs-string">"2017-12-05T04:26:44.584Z"</span>), formatStr);
        }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>   
</code></pre>
</docs-demo-block>
<h3 id="storage-cao-zuo"><a class="header-anchor" href="#storage-cao-zuo" aria-hidden="true">¶</a> Storage 操作</h3>
<p><code v-pre="">localStorage</code> 和 <code v-pre="">sessionStorage</code> 仅是对 <code v-pre="">window.localStorage</code> 和 <code v-pre="">window.sessionStorage</code> 的一个封装：</p>
<pre v-pre=""><code class="hljs language-js"><span class="hljs-comment">// 写入的 key 均会有私有前缀 _fm_</span>
<span class="hljs-keyword">this</span>.$utils.localStorage.set(<span class="hljs-string">'name'</span>, <span class="hljs-string">'test'</span>);  <span class="hljs-comment">// 写</span>
<span class="hljs-keyword">this</span>.$utils.localStorage.get(<span class="hljs-string">'name'</span>); <span class="hljs-comment">// 读 =&gt; 'test'</span>
<span class="hljs-keyword">this</span>.$utils.localStorage.set(<span class="hljs-string">'name2'</span>, <span class="hljs-string">'test2'</span>); 
<span class="hljs-keyword">this</span>.$utils.localStorage.getAll(); <span class="hljs-comment">// 读取所有 =&gt; {name: 'test', name2: 'test2'}</span>
<span class="hljs-keyword">this</span>.$utils.localStorage.remove(<span class="hljs-string">'name2'</span>); <span class="hljs-comment">// 清除指定的 key</span>
<span class="hljs-keyword">this</span>.$utils.localStorage.clear(); <span class="hljs-comment">// 清除所有 key</span>
</code></pre>
<p><strong>需要注意的是 <code v-pre="">memoryStorage</code> 只是内存中的一个对象，可用于临时存储应用数据，但不可用于持久化数据。</strong></p>
</section></template>

<script>
  export default {
    methods: {
        avatarError(e) {
            return this.$utils.avatarError(e);
        },

        avatarError2 (e) {
            return this.$utils.avatarError(e, '//www.followme.com/portalindex/assets/logo.png');
        },

        getCurDate () {
            return new Date("2017-12-05T04:26:07.584Z");
        },

        formatDate (formatStr = '') {
            return this.$utils.formatDateToStr(new Date("2017-12-05T04:26:07.584Z"), formatStr);
        }
    }
  }
</script>


